.app {
  margin: 0;
  width: 100%;
  background-color: rgb(244, 241, 252);
  padding: 15px;
}

.page {
  text-align: center;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;

  margin-left: auto;
  margin-right: auto;

  -webkit-box-shadow: 1px 1px 10px 0px rgba(34, 60, 80, 0.2);
  -moz-box-shadow: 1px 1px 10px 0px rgba(34, 60, 80, 0.2);
  box-shadow: 1px 1px 10px 0px rgba(34, 60, 80, 0.2);

  width: 75%;
  height: 100vh;
  padding: 10px;
}

.left-column {
  float: left;
  border: 1px solid black;
}

.main-column {
  float: right;
  border: 1px solid red;
}
